// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property(cc.Prefab)
    roundPrefab: cc.Prefab = null;

    @property(cc.Node)
    background: cc.Node = null;

    @property(cc.Node)
    player : cc.Node = null;

    @property(cc.Prefab)
    congratsprefab : cc.Prefab =null;

    @property(cc.AudioClip)
    congratsAudio : cc.AudioClip = null;

    @property(cc.Sprite)
    progress : cc.Sprite = null;
    leftrotate:boolean;
    rightrotate: boolean;
    shots = 0;

    progress_frac = 0;

    num_rounds;

    // LIFE-CYCLE CALLBACKS:

     onLoad () {
         this.spawnNewRound();
         this.shots = 0;
         this.progress.fillRange = 0;
         this.node.on( 'touchstart', function ( event) {
            let a=event.touch.getLocationX();
            if (a<=480)
            this.leftrotate= true;
            if (a>480)
            this.rightrotate=true;
          }, this);
          this.node.on( 'touchend', function ( event) {
            this.leftrotate= false;
            this.rightrotate=false;
          }, this);
        }

    spawnNewRound(){
        // Generate a new node in the scene with a preset template

        let n= Math.ceil(Math.random()*5 + 4);
        this.num_rounds =n;
        //this.roundno=n;
        for(let i=0; i<n;i++)
        {
            //let name: string ='newStar' + i.toString();
            let newRound = cc.instantiate(this.roundPrefab);
            this.node.addChild(newRound);
            newRound.setPosition(this.getNewRoundPosition()); 
            newRound.getComponent('round').game = this;   
        }
        

    }

    gainScore() {
        // Update the words of the scoreDisplay Label
        this.shots+=1;
        this.progress_frac = this.shots/this.num_rounds;
        this.progress.fillRange = this.progress_frac;

        if(this.shots == this.num_rounds)
        {
                this.congrats();
        }
        
    }

    congrats()
    {
        let newlabel  =cc.instantiate(this.congratsprefab);
        this.node.addChild(newlabel);
        this.node.getChildByName('player').destroy();
        this.node.getChildByName('button').destroy();
        cc.audioEngine.playEffect(this.congratsAudio, false);
        this.shots = 0;    
    }

    getNewRoundPosition() {
        var randX = 0;
        
        var randY = Math.random() * (this.node.height/2-20);
       
        var maxX = this.background.width/2;
        //randX = (Math.random() - 0.5) * 2 * maxX;

        randX = (Math.random()-0.5)*2*(maxX-20);
       
        return cc.v2(randX, randY);
    }

    start () {

    }

     update (dt) {
        if(this.leftrotate==true)
        this.player.angle+=10*dt;
        else if(this.rightrotate==true)
        this.player.angle-=10*dt; 
     }
}
