//  - https://docs.cocos.com/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property(cc.Node)
    shotspawn : cc.Node = null;

    @property(cc.Prefab)
    fireprefab : cc.Prefab = null;

    @property(cc.Node)
    button : cc.Node = null;

    @property(cc.Node)
    firespawn: cc.Node = null;

    @property(cc.AudioClip)
    laserAudio: cc.AudioClip = null;

    @property
    rotateSpeed: number = 0;

    

    accLeft: boolean = false;
    accRight: boolean = false;

    onKeyDown (event: { keyCode: any; }) {
        // Set a flag when key pressed
        switch(event.keyCode) {
            case cc.macro.KEY.a:
            case cc.macro.KEY.left:
                this.accLeft = true;
                break;
            case cc.macro.KEY.d:
            case cc.macro.KEY.right:
                this.accRight = true;
                break;
            case cc.macro.KEY.enter:
                this.bulletfire();
        }
    }

    onKeyUp (event: { keyCode: any; }) {
        // Unset a flag when key released
        switch(event.keyCode) {
            case cc.macro.KEY.a:
            case cc.macro.KEY.left:
                this.accLeft = false;
                break;
            case cc.macro.KEY.d:
            case cc.macro.KEY.right:
                this.accRight = false;
                break;
        }
    }

    bulletfire()
     {
         let newfire: cc.Node = cc.instantiate(this.fireprefab);
         this.firespawn.addChild(newfire);
         newfire.setPosition(0,0);
         newfire.angle = 0;
         cc.audioEngine.playEffect(this.laserAudio, false);
     }
 
    // LIFE-CYCLE CALLBACKS:
    
     onLoad () {
        //this.accLeft = false;
        //this.accRight = false;
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
 
     }

     onDestroy () {
        // Cancel keyboard input monitoring
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    }

    start () {    
    }

     update (dt:number) 
     {
        if (this.accLeft) 
        {
            this.node.angle += this.rotateSpeed*dt;
        } 
        else if (this.accRight) 
        {
            this.node.angle -= this.rotateSpeed*dt;
        }   
     }
}
