const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property
    pickRadius: number = 0;

    @property(cc.AudioClip)
    GameOverAudio: cc.AudioClip = null;

    @property(cc.Prefab)
    GameOverprefab : cc.Prefab =null;

    game = null;

    Borderbelow: number = -270;

    public flag = false;
    // LIFE-CYCLE CALLBACKS:


     onLoad () {
        //this.game.score =0;
        //this.game = this.node.parent.getComponent('player');

        let manager = cc.director.getCollisionManager();
        manager.enabled = true;
        this.flag = false;
        
     }

     onCollisionEnter(other) {
      //this.game.score+=1;
      
      this.node.destroy();
      this.game.gainScore();
  
   }

   GameOver()
    {
        if(this.node.parent.getChildByName('player'))
        {
        this.node.parent.getChildByName('player').destroy();
        this.node.parent.getChildByName('button').destroy();
        let gameoverlabel = cc.instantiate(this.GameOverprefab);
        this.node.parent.addChild(gameoverlabel);
        }
        // cc.audioEngine.playEffect(this.GameOverAudio, false);
        // this.node.parent.getChildByName("newRound").destroy();
    }

    start () {

    }

     update (dt) {

        if (this.node.y < this.Borderbelow && this.flag==false)
        {
            this.flag=true;
            this.GameOver();
           
        }
        else if (this.flag==false)
        {
         this.node.y -= dt*10;
        }
     }
}
